/**
 * YouTube
 */
class Queue {
    /**
     * singleton
     */
    constructor() {
        // 
        this.index = 0;
        this.queue = [];
    }

    /**
     * 
     * @param {string} videoId - ID
     */
    add(videoId) {
        this.queue.splice(this.index, 0, videoId);
    }

    /**
     * 
     * @param {number} index - 
     */
    remove(index) {
        this.queue.splice(index, 1);
    }
}

module.exports = new Queue();



